// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import Vuex from 'vuex';
import store from './store';
import firebase from './firebase'


import VueMaterial from 'vue-material'
import 'vue-material/dist/vue-material.min.css'
import 'vue-material/dist/theme/default.css';
import Vuefire from 'vuefire';

Vue.use(Vuefire)
Vue.use(VueMaterial)

Vue.use(Vuex);
Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  data: {
    active_tokens:[],
    unassigned: true,
    pending:true
  },
  firebase: {
    companies: firebase.database().ref('companies'),
    users: firebase.database().ref('users'),
    tokens: firebase.database().ref('tokens')
  },
  mounted(){
 var self = this
    setInterval(function(){ 
      if(self.active_tokens.length < 1){
        // console.log('====================================');
        // console.log('NO ONE IS BOOKING, HAHAHAHA');
        // console.log('====================================');
      }else{
        firebase.database().ref('tokens/'+self.active_tokens[0].company_key+"/"+self.active_tokens[0].token_key).on('value', snap => {
          // console.log('====================================');
          // console.log(snap.val());
          // console.log('====================================');

          let tkn = snap.val()
          if(tkn.token != 'unassigned'){

            if(self.unassigned){
            self.sendNotification("TOKEN ASSIGNED", "YOUR TOKEN NUMBER IS "+tkn.token+". There are "+tkn.pos+" Ahead of You. Waiting time 20 Minutes. Kindly await your turn");
                self.unassigned = false
            }
          }else{
            self.unassigned = true
          }
          if(tkn.status != 'pending'){
            if(self.pending){
              self.sendNotification("ITS YOUR TURN", "TOKEN NUMBER"+tkn.token+", Please proceed to counter number"+tkn.counter+". Thank you");
              self.pending = false
            }

          }else{
            self.pending = true
          }

        });
      }
     }, 3000);
     
  },
  methods: {
    sendNotification(title,msg){
      console.log('==============NOTIFICATION======================');
      console.log(title + " == " + msg);
      console.log('====================================');
      navigator.serviceWorker.ready.then(registration => {
        const title = title;
        const options = {
          body: msg,
          icon: '/static/img/logo.3f98c24.png'
        };
        registration.showNotification(title, options);

     });
    }
  },
  router,
  template: '<App/>',
  components: { App}
})



