<template>
  <div class="page-container">
    <md-app md-mode="reveal">
      <md-app-toolbar class="md-transparent" md-elevation="0">
        <md-button class="md-icon-button toggle-button">
         <router-link to="/tokens"> <md-icon>arrow_back</md-icon> </router-link>
        </md-button>
        <span class="md-title">Token</span>
      </md-app-toolbar>

      <md-app-content>
<md-card>
      <md-card-header>
        <md-avatar>
        <md-icon>library_books</md-icon>
        </md-avatar>

      </md-card-header>


      <md-card-content style="margin: 40px !important;text-align:  center;padding: 20px;">
          <br>

           <span style="font-size:  300%;font-weight:  bolder;font-family:  monospace; padding: 10px 0px 10px 0px;">TOKEN NO <br><br> {{ myToken.token }}</span><br><br><br>

      <span style="font-size:  200%;font-weight:  bolder;font-family:  monospace; padding: 10px 0px 10px 0px;">{{ myToken.counter }}</span>
      <br>
      <p style="padding: 10px 0px 10px 0px;"> {{ myToken.service }}</p>
      <br>
      
      <span style="padding: 10px 0px 10px 0px;">There are  <md-chip class="md-primary" md-clickable>{{ myToken.pos}}</md-chip>  customers ahead of you</span>
      <br>
       
       <h1 style="font-size:  140%;font-weight:  bolder;font-family:  monospace;">20 MIN</h1>
                          <p style="padding: 10px 0px 10px 0px;">Average waiting time</p>
      </md-card-content>

      <md-card-actions>
        <md-button>Discard</md-button>
         <md-button @click="onCloseToken">
           Home
       </md-button>
      </md-card-actions>
    </md-card>

      
        </md-app-content>
    </md-app>
  </div>
</template>

<style scoped>
.md-app-content .md-card{
  background-image: url(/static/img/receipt.png);
    background-size: contain;
    background-repeat-y: no-repeat;
    box-shadow: none !important;
 }
.md-app {
     height: 100vh;
   
    
  }
  .md-app-content {
    padding: 0px !important;
}
</style>

<script>
//global registration
import Vue from 'vue'



export default {
  name: 'Token',
  data: () => ({
   myToken: {
          department: '',
                  service: '',
                  d_id: '',
                  s_id:'' ,
                  status: 'pending',
                  token: 'unassigned',
                  pos: '0',
                  counter: 'Counter1'
    },
  }),
  mounted(){
    var self = this
     setInterval(function(){ 
      if(self.$root.$data.active_tokens.length < 1){

        self.hasToken = true
        // console.log('====================================');
        // console.log('NO ONE IS BOOKING, HAHAHAHA');
        // console.log('====================================');
      }else{
       self.myToken = self.$root.$data.active_tokens[0]
      }
     }, 3000);
     
    
  },
  methods: {
  onCloseToken(){
      this.$router.push('/')
  }
  }
}
</script>


