<template>
  <div class="page-container">
          <md-toolbar class="md-transparent"  v-show="connectivityStatus" md-elevation="0" style="
    font-size:  120%;
    font-weight:  bolder;
    justify-content:  center;
    align-items:  center;
    color:  #666;
">

 <span class="md-title">{{ connectivityText }}</span>
</md-toolbar>
   
    
     <router-view></router-view>
  </div>
</template>

<script>
require('./assets/themify-icons/themify-icons.min.css')

require('./assets/vendor/bootstrap/css/bootstrap.min.css')
require('./assets/fonts/font-awesome-4.2.0/css/font-awesome.min.css')
require('./assets/fonts/iconic/css/material-design-iconic-font.min.css')


var logo = require('./assets/logo.png')
export default {
  name: 'app',
  
  data() {
    return {
      logo: logo,
      menuVisible: false,
      connectivityText: '',
      connectivityStatus: false
    }
  },
  created(){
     window.addEventListener('offline', () => {
          this.connectivityStatus = true;
          this.connectivityText = 'You seem to be offline. Connect to Use the app status';
        })
        window.addEventListener('online', () => {
        //  alert('YAYA')
          this.connectivityStatus = false;
        })
  },
  methods: {
  
  }
}

	
</script>

<style>
a {
  color: #666 !important;
  text-decoration: none !important;
}
@media (max-width: 576px)
{
.wrap-login100 {
    /* padding: 77px 15px 33px 15px; */
    padding: 40px !important;
    border-radius: 0px !important;
    margin-top: -50px !important;
}
}

.container-login100{
  padding: 0px !important;
   min-height: 90vh !important;
   background: #fff;
}
::-webkit-scrollbar { 
    display: none; 
}
</style>



