<template>
  <div class="page-container">
    <md-app md-mode="reveal">
      <md-app-toolbar class="md-transparent" md-elevation="0">
        <md-button class="md-icon-button toggle-button">
         <router-link to="/home"> <md-icon>arrow_back</md-icon> </router-link>
        </md-button>
        <span class="md-title">{{ company.name }}</span>
      </md-app-toolbar>

      <md-app-content>
         <form-wizard @on-complete="onComplete"
                      @on-loading="setLoading"
                      @on-validate="handleValidation"
                      @on-error="handleErrorMessage"
                      :start-index="0"
                      title=""
                      subtitle=""
                      error-color="#e74c3c" 
                      color="#448aff">
            <tab-content title="Select service"
                         icon="ti-user" :before-change="validateAsync">
                          
                                  <md-chip class="md-accent" md-deletable v-show="alert"> <strong>Invalid! {{ errorMsg }}.</strong> </md-chip>
                         <div class="alert alert-warning media fade in" v-show="noservice"><button class="close"><i class="pci-cross pci-circle"></i></button>
					                            <strong>Sorry!</strong> <p >No Services are available, Please check again Later!</p>
                                                  
					                        </div>
                                  <md-progress-bar md-mode="indeterminate" v-show="loadingWizard"></md-progress-bar>
                                   <div class="col-md-12" v-show="!noservice">
                                      <md-steppers md-vertical style="text-align: left !important;">
                                      <md-step  v-for="department in company.departments" :md-label="department.name" md-description="Serviced by your teller " :key="department.id" @click="setDepartment(department)" :id="department.name">
                                        <div v-for="service in department.services" :key="service.id"  @click="setService(service)">
                                            <md-radio v-model="radio" :value="department.id +','+ service.id" class="md-primary">{{ service.name }}<small></small></md-radio>
                                        </div>
                                      </md-step>

                                      <md-step id="second" md-label=""  md-description=""></md-step>

                                    </md-steppers>
                                   </div>
           
            </tab-content>
            <tab-content title="Enter Code"
                         icon="ti-settings" style="text-align: center" :before-change="validateToken">
                          <p style="padding: 10px 0px 10px 0px;">PLEASE ENTER THE TODAY'S CODE TO CONTINUE</p>
             <md-content class="md-content2">

                <span class="input input--kuro">
                  <input class="input__field input__field--kuro" type="text" id="input-7" />
                  <label class="input__label input__label--kuro" for="input-7">
                    <span class="input__label-content input__label-content--kuro">CODE</span>
                  </label>
                </span>
             </md-content>
            
            </tab-content>
            <tab-content title="Your Token" icon="ti-check">
              <md-card>
                   <md-card-content style="margin: 40px !important;text-align:  center;padding: 20px;">
                            <br>

                            <span style="font-size:  300%;font-weight:  bolder;font-family:  monospace; padding: 10px 0px 10px 0px;">MESSAGE <br> </span>

                        
                        <p style="padding: 10px 0px 10px 0px;">{{ myToken.service }}</p>
                        <br>
                        
                        <span style="padding: 10px 0px 10px 0px;">Ypu will be notified once the service start. Please await yor token number patiently.</span>
                        <br>
                        
                        <h1 style="font-size:  140%;font-weight:  bolder;font-family:  monospace;">VQMS</h1>
                                            <p style="padding: 10px 0px 10px 0px;">&copy; 2018 Michael</p>
                        </md-card-content>
              </md-card>
            </tab-content>
        </form-wizard>
        </md-app-content>
    </md-app>
  </div>
</template>

<script>
//global registration
import Vue from 'vue'
import VueFormWizard from 'vue-form-wizard'
import 'vue-form-wizard/dist/vue-form-wizard.min.css'
Vue.use(VueFormWizard)


export default {
  name: 'Reveal',
  data: () => ({
    menuVisible: false,
    radio: null,
    company: {
      name: '',
      departments: []
    },
    myToken: {
          department: '',
                  service: '',
                  d_id: '',
                  s_id:'' ,
                  status: 'pending',
                  token: 'unassigned',
                  pos: '0',
                  counter: 'Counter1'
    },
    default_service: null,
    default_department: null,
    navmenu: false,
    noservice: false,
    services: null,
    pos: 0,
    token: {
            number:'1',
            phone: '2',
            status: 'active'
        },
    loadingWizard: false,
    errorMsg: null,
    alert: false,
    core_data: {
          queue_id: '',
          phone: '+254',
          service: ''
      }
  }),
  mounted(){
    this.company.key = this.$route.params.name;
    var self = this
      this.$root.$firebaseRefs.companies.child(this.company.key).on('value', (snap) => {

        for (const departk in snap.val().departments) {
          if (snap.val().departments.hasOwnProperty(departk)) {
            const element = snap.val().departments[departk];
            
            let department = {
              name: element.name,
              id: element.id,
              services: []
            };
            console.log('====================================');
            console.log(department);
            console.log('====================================');

            for (const servicek in element.services) {
              if (element.services.hasOwnProperty(servicek)) {
                const e = element.services[servicek];
                
                let s = { 
                  name:  e.name,
                  id:  e.id
                };
              
                department.services.push(s)
              }
            }
            self.company.departments.push(department);
          }
          console.log('==========Departments==========================');
          console.log( self.company.departments);
          console.log('====================================');
        }
        
        self.company.name = snap.val().name


      });

  },
  methods: {
  onComplete: function(){
      // alert('Yay. Done!');

      console.log('====================================');
      console.log(this.radio);
      console.log('====================================');
      this.$router.push('token');
   },
   setService(service){
     console.log('==============Service======================');
     console.log(service);
     console.log('====================================');

     this.default_service = service
   },
   setDepartment(department){
     this.default_department = department;
     console.log('=============Department=======================');
     console.log( this.default_department.name);
     console.log('====================================');
   },
   setLoading: function(value) {
            this.loadingWizard = value
        },
        handleValidation: function(isValid, tabIndex){
           console.log('Tab: '+tabIndex+ ' valid: '+isValid)
        },
        handleErrorMessage: function(errorMsg){
          this.errorMsg = errorMsg
        },
        validateToken:function() {
          return new Promise((resolve, reject) => {

                //  console.log('==============IM IN A TOKEN======================');
                // console.log(this.default_service.name);

              
              var id = this.radio.split(',')[0]
              let tokensRef = this.$root.$firebaseRefs.tokens.push().key;
                let tokenData = {
                  department: this.default_department.name,
                  service: this.default_service.name,
                  d_id: this.default_department.id,
                  s_id: this.default_service.id,
                  status: 'pending',
                  token: 'unassigned',
                  pos: '0',
                  counter: 'Counter1'
                }

                this.myToken = tokenData;
                var updates = {};
                updates['/' + this.company.key +'/'+tokensRef] = tokenData ;
                this.$root.$firebaseRefs.tokens.update(updates);
                this.$root.$data.active_tokens[0] = {
                    company_key: this.company.key,
                    token_key: tokensRef,
                     department: this.default_department.name,
                  service: this.default_service.name,
                  d_id: this.default_department.id,
                  s_id: this.default_service.id,
                  status: 'pending',
                  token: 'unassigned',
                  pos: '0',
                  counter: 'Counter1'
                  };
                // console.log('==============ROOT DATA======================');
                // console.log(this.$root.$data);
                // console.log('=================yyyy===================');
                resolve(true)
    // this.$root.$firebaseRefs.companies.on('value', (snap) => {
    //                 console.log('====================================');
    //                 console.log(snap.val());
              // console.log('======== this is the tokens ref============================');
              // console.log(tokensRef);
              // console.log('====================================');
//  this.$http.post('http://localhost:8000/api/token',
          //     {
          //   queue_id: id,
          //   token_phone:  "+254",
          //   user_id: "1",



          //   token_task: this.default_service.name
          //     }).then(response => {
          //                           console.log('=============hhhhhh=======================');
          //                           console.log(response);
          //                           console.log('====================================');
          //                            resolve(true)
                                    
          //                     })
          //                     .catch(error => {
          //                         console.log('=================Error===================');
          //                         console.log(error);
          //                         console.log('====================================');
                             
          //                         reject('There Was an error generating your token! Try again Later')
          //                     });
          
         
          })
         },
        validateAsync:function() {
          return new Promise((resolve, reject) => {
            setTimeout(() => {
              if(this.radio == null ){
               this.alert = true
               
                  reject('You Must Select a service to continue!')
              }else{
               
               this.alert = false
               resolve(true)
              }   
            }, 500)
          })
         },
  }
}
</script>


<style scoped>
 .md-content2 {
    width: 100%;
    height: 200px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    text-align: center !important;
  }
.md-app {
     height: 100vh;
    
  }
  .md-app-content {
    padding: 0px !important;
}
.vue-form-wizard .wizard-header {
    padding: 0px !important;
    }

input {
	position: relative;
	z-index: 1;
	display: inline-block;
	margin: 1em;
	max-width: 350px;
	width: calc(100% - 2em);
	vertical-align: top;
}

.input__field {
	position: relative;
	display: block;
	float: right;
	padding: 0.8em;
	width: 60%;
	border: none;
	border-radius: 0;
	background: #f0f0f0;
	color: #aaa;
	font-weight: bold;
	font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
	-webkit-appearance: none; /* for box shadows to show on iOS */
}

.input__field:focus {
	outline: none;
}

.input__label {
	display: inline-block;
	float: right;
	padding: 0 1em;
	width: 40%;
	color: #6a7989;
	font-weight: bold;
	font-size: 150%;
	-webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-khtml-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}

.input__label-content {
	position: relative;
	display: block;
	padding: 1.6em 0;
	width: 100%;
}

.graphic {
	position: absolute;
	top: 0;
	left: 0;
	fill: none;
}

.icon {
	color: #ddd;
	font-size: 150%;
}

/* Kuro */
.input--kuro {
	max-width: 320px;
	margin-bottom: 3em;
}

.input__field--kuro {
	width: 100%;
	background: transparent;
	color: #9196A1;
	opacity: 0;
	text-align: center;
	-webkit-transition: opacity 0.3s;
	transition: opacity 0.3s;
}

.input__label--kuro {
	position: absolute;

	width: 60%;
	color: #666;
	pointer-events: none;
}

.input__label--kuro::before,
.input__label--kuro::after {
	content: '';
	position: absolute;
	top: 0;
	left: 0;
	width: 50%;
	height: 100%;
	border: 4px solid #747981;
	-webkit-transition: -webkit-transform 0.3s;
	transition: transform 0.3s;
}

.input__label--kuro::before {
	border-right: none;
}

.input__label--kuro::after {
	left: 50%;
	border-left: none;
}

.input__field--kuro:focus,
.input--filled .input__field--kuro {
	opacity: 1;
	-webkit-transition-delay: 0.3s;
	transition-delay: 0.3s;
}

.input__field--kuro:focus + .input__label--kuro::before,
.input--filled .input__label--kuro::before {
	-webkit-transform: translate3d(-10%, 0, 0);
	transform: translate3d(-10%, 0, 0);
}

.input__field--kuro:focus + .input__label--kuro::after,
.input--filled .input__label--kuro::after {
	-webkit-transform: translate3d(10%, 0, 0);
	transform: translate3d(10%, 0, 0);
}

.input__field--kuro:focus + .input__label--kuro .input__label-content--kuro,
.input--filled .input__label-content--kuro {
	-webkit-animation: anim-2 0.3s forwards;
	animation: anim-2 0.3s forwards;
}

@-webkit-keyframes anim-2 {
	50% {
		opacity: 0;
		-webkit-transform: scale3d(0.3, 0.3, 1);
		transform: scale3d(0.3, 0.3, 1);
	}
	51% {
		opacity: 0;
		-webkit-transform: translate3d(0, 3.7em, 0) scale3d(0.3, 0.3, 1);
		transform: translate3d(0, 3.7em, 0) scale3d(0.3, 0.3, 1);
	}
	100% {
		opacity: 1;
		-webkit-transform: translate3d(0, 3.7em, 0);
		transform: translate3d(0, 3.7em, 0);
	}
}

@keyframes anim-2 {
	50% {
		opacity: 0;
		-webkit-transform: scale3d(0.3, 0.3, 1);
		transform: scale3d(0.3, 0.3, 1);
	}
	51% {
		opacity: 0;
		-webkit-transform: translate3d(0, 3.7em, 0) scale3d(0.3, 0.3, 1);
		transform: translate3d(0, 3.7em, 0) scale3d(0.3, 0.3, 1);
	}
	100% {
		opacity: 1;
		-webkit-transform: translate3d(0, 3.7em, 0);
		transform: translate3d(0, 3.7em, 0);
	}
}


</style>



