* { box-sizing: border-box; }

body { font-family: sans-serif; }

.carousel {
  background: #EEE;
}
.carousel-cell {
  width: 90%; /* half-width */
  height: auto;
  margin-right: 5px;
}

@-moz-keyframes pulsate {
  from {
    -moz-transform: scale(0.25);

    opacity: 1;
  }

  95% {
    -moz-transform: scale(1.3);

    opacity: 0;
  }

  to {
    -moz-transform: scale(0.3);

    opacity: 0;
  }
}

@-webkit-keyframes pulsate {
  from {
    -webkit-transform: scale(0.25);

    opacity: 1;
  }

  95% {
    -webkit-transform: scale(1.3);

    opacity: 0;
  }

  to {
    -webkit-transform: scale(0.3);

    opacity: 0;
  }
}

/* get the container that's just outside the marker image, 
which just happens to have our Marker title in it */

#mapmy div.gmnoprint[title="location"] {
  -moz-animation: pulsate 1.5s ease-in-out infinite;

  -webkit-animation: pulsate 1.5s ease-in-out infinite;

  border: 100px solid aqua;

  /* make a circle */

  -moz-border-radius: 50%;

  -webkit-border-radius: 50%;

  border-radius: 50%;

  /* multiply the shadows, inside and outside the circle */

  -moz-box-shadow: inset 0 0 5px aqua,
    inset 0 0 5px aqua, inset 0 0 5px aqua, 0 0 5px aqua,
    0 0 5px aqua, 0 0 5px aqua;

  -webkit-box-shadow: inset 0 0 5px aqua,
    inset 0 0 5px aqua, inset 0 0 5px aqua, 0 0 5px aqua,
    0 0 5px aqua, 0 0 5px aqua;

  box-shadow: inset 0 0 5px aquamarine, inset 0 0 5px aqua,
    inset 0 0 5px aqua, 0 0 5px aqua, 0 0 5px aqua, 0 0 5px aqua;

  /* set the ring's new dimension and re-center it */

  height: 11px !important;

  margin: -90px 0 0 -90px;

  width: 51px !important;
}

/* hide the superfluous marker image since it would expand and shrink with its containing element */

/*	#mapmy_canvas div[style*="987654"][title] img {*/

#mapmy div.gmnoprint[title="location"] img {
  display: none;
}

</style>



