<template>
<Slideout menu="#menu" panel="#panel" :toggleSelectors="['.toggle-button']" @on-open="open">
      <nav id="menu">
         <md-toolbar class="md-transparent" md-elevation="0" style="
    font-size:  120%;
    font-weight:  bolder;
    justify-content:  center;
    align-items:  center;
    color:  #666;
">
             <md-avatar style="width: 100px; height: 100px; border-radius: 0px !important">
          <img src="/static/img/icons/logo.png" alt="Avatar" >
        </md-avatar>
         <md-divider class="md-inset"></md-divider> 
             <md-card-header>
          <div class="md-subhead">VIRTUAL QUEUE MANAGEMENT SYSTEM</div>
        </md-card-header>
            </md-toolbar>
      
        <md-list>
          <md-list-item>
            <md-icon>home</md-icon>
            <router-link to="/home" class="md-list-item-text">Home</router-link>
         
          </md-list-item>

          <md-list-item>
            <md-icon>library_books</md-icon>
            <router-link to="/tokens" class="md-list-item-text">My Tokens</router-link>
          </md-list-item>

        </md-list>
      </nav>
      <div id="panel">
      <md-app md-mode="reveal">

          <md-app-toolbar class="md-transparent" md-elevation="0">
                  <md-button class="md-icon-button toggle-button">
                    <md-icon>menu</md-icon>
                  </md-button>
                  <span class="md-title">TOKENS</span>
                </md-app-toolbar>
                <md-app-content>
                     <md-list class="md-triple-line" >
                    <md-ripple>
                      <md-list-item v-show="!hasToken">
                        <md-avatar>
                          <md-icon>dvr</md-icon> 
                        </md-avatar>
                    <md-chip class="md-accent" md-deletable> <strong>Empty! No tokens available Today!.</strong> </md-chip>
                        
                      </md-list-item>
                      <md-list-item @click="viewToken" v-show="hasToken">
                        <md-avatar>
                          <md-icon>dvr</md-icon> 
                        </md-avatar>

                        <div class="md-list-item-text" style="text-align: center">
                          <span style="font-size:  140%;font-weight:  bolder;font-family:  monospace;">{{ myToken.department }}</span>
                          <p style="padding: 10px 0px 10px 0px;"> {{ myToken.service }}</p>
                          
                           <span style="font-size:  110%;font-weight:  bolder;font-family:  monospace; padding: 10px 0px 10px 0px;">TOKEN NUMBER #{{ myToken.token }}</span>
                          <span style="padding: 10px 0px 10px 0px;">+{{ myToken.pos }} PEOPLE Ahead of you</span>
                          
                        </div>

                        <md-button class="md-icon-button md-list-action">
                          <md-icon class="md-primary">fiber_manual_record</md-icon>
                        </md-button>
                    </md-list-item>
                    </md-ripple>
                  <md-divider class="md-inset"></md-divider> 
                    
                </md-list>
                </md-app-content>
      </md-app>
       
      </div>
    </Slideout>
 
	
</template>

<script>

import Slideout from 'vue-slideout'

export default {
  name: 'hello',
  components: {
      Slideout 
    },
  data () {
    return {
      msg: 'Welcome to Your Vue.js PWA',
		hasToken: false,
   myToken: {
          department: '',
                  service: '',
                  d_id: '',
                  s_id:'' ,
                  status: 'pending',
                  token: 'unassigned',
                  pos: '0',
                  counter: 'Counter1'
    },
    }
  },
    mounted(){
 var self = this
     setInterval(function(){ 
      if(self.$root.$data.active_tokens.length < 1){

        self.hasToken = false
        // console.log('====================================');
        // console.log('NO ONE IS BOOKING, HAHAHAHA');
        // console.log('====================================');
      }else{
        self.hasToken = true
       self.myToken = self.$root.$data.active_tokens[0]
      }
     }, 3000);
		
	},
	methods: {
		open: function () {
        console.log('slideoutOpen')
            },
            viewToken(){
                this.$router.push('token');
            }
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
*:focus { outline: none !important; }

* {
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);  
} 
.slideout-menu {
    position: fixed;
    top: 0;
    bottom: 0;
    width: 256px;
    height: 100vh;
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
    z-index: 0;
    display: none;
    background: #fff;
    box-shadow: inset -1px -1px 3px 0px #909090;

    padding-right: 3px;

  }

  .slideout-menu-left {
    left: 0;
  }

  .slideout-menu-right {
    right: 0;
  }

  .slideout-panel {
    background-color: #fff;
    color: white;
    position: relative;
    z-index: 1;
    will-change: transform;
    min-height: 100vh;
  }

  .slideout-open,
  .slideout-open body,
  .slideout-open .slideout-panel {
    overflow: hidden;
  }

  .slideout-open .slideout-menu {
    display: block;
  }
.md-app {
     height: 100vh;
    border: 1px solid rgba(#000, .12);
  }
.md-drawer {
    width: 230px;
    max-width: calc(100vw - 125px);
  }
  .md-app-content {
    padding: 0px !important;
}


</style>



